import { Rating } from '~/store/modules/auth/types';

const getLevelType = (currentRating: Rating) => {
  if (currentRating === 'ASSISTANT_MANAGE') return '';
  else if (currentRating === 'DEPARTMENT_HEAD') return '';
  else if (currentRating === 'MANAGING_DIRECTOR') return '';
  else if (currentRating === 'BOSS') return '';
  else return '';
};
export default getLevelType;
